<template>
    <div>
        <el-cascader
            style="width: 100%"
            :size="size"
            :options="options"
            @change="cityChange"
            :value="value"
            >
        </el-cascader>
    </div>
</template>

<script>
    import { regionData } from './data.js'
    export default {
        name: 'CityCascader',
        props: {
            value: Array,
            size: {
                type: String,
                default: 'small'
            }
        },
        data:function () {
            return {
                options: regionData,
            }
        },
        methods:{
            cityChange(val){
                this.$emit('input',val)
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>
